#grid-container {
    position: absolute;   height: 100%;
    left: 0;
    right: 0px;
    top: 0;
    bottom: 0;
    overflow: hidden;    z-index: 0;
    .grid-columns {
        position: absolute;   height: 100%;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        white-space: nowrap; z-index: 0;
        .column {
            height: 100%;
            position: relative;  z-index: 0;   display: inline-block;
            &::before {
                content: '';
                position: absolute;
                left: 0;
                top: 0;
                bottom: 0;
                width: 1px;
                background: rgba(255,255,255,0.1);
            }
        }
    }

    .gridDots {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0; z-index: 0;
    .dot {
            position: absolute; z-index: 0;
            width: 1.5px;
            height: 1.5px;
            background: rgba(255,255,255,0.3);
            border-radius: 50%;
        }
    }

    &.green{

       
        .grid-columns {
            .column {
                &::before {background: rgba( 0, 116, 122,0.08);}
            }

        }
        .gridDots {
            .dot {  background: rgba(0, 116, 122,0.8);}
        }

    }
}


