                top:2.5rem;
                width: 100%;
                height: 3px;
                bottom: 0;
                left: 0;
                background-color: #ffa150;
                opacity: 1;
                transform-origin: 0 50%;
    transform: scaleX(0);
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
               
            }

        
            &:after {
                content: '';
                position: absolute;
                top: 2.5rem;
                width: 100%;
                height: 3px;
                bottom: 0;
                left: 0;
                background-color: #ffa150;
                opacity: 1;
                transform-origin: 100% 50%;
    transform: scaleX(1);
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.25s;
               
            }

            &:hover{
                &:before{
                    transform: scaleX(1);
                    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.25s;
                }
               
                &:after{
                    transform: scaleX(0);
                    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
                }
            }
        }
    }
