/* The animation  */
@keyframes showfade {
    0%   {opacity: 0;}
    25%   {opacity: 0.25}
    50%   {opacity: 0.5}
    100%   {opacity: 1}
}



@keyframes pulse {
    0% {
        -moz-box-shadow: 0 0 0 0 rgba(255,161,80,0);
        box-shadow: 0 0 0 0 rgba(255,161,80,0);
        
      }

    20% {
        -moz-box-shadow: 0 0 0 0 rgba(255,161,80,0.5);
        box-shadow: 0 0 0 0 rgba(255,161,80,0.5);
        
      }
    50% {
        -moz-box-shadow: 0 0 0 10px rgba(255,161,80,0);
        box-shadow: 0 0 0 10px rgba(255,161,80,0);
    }
    70% {
        -moz-box-shadow: 0 0 0 0 rgba(255,161,80,0.5);
        box-shadow: 0 0 0 0 rgba(255,161,80,0.5);
        
      }
    100% {
        -moz-box-shadow: 0 0 0 0 rgba(255,161,80,0);
        box-shadow: 0 0 0 0 rgba(255,161,80,0);
    }
  }

@keyframes beforewidthYellow {
    0%   {width: 0px;}
    25%   {width: 20vw}
    50%   {width: 40vw}
    70%   {width: 60vw;}
    100%   {width: 80vw;}
}